* {
  margin: 0px;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
}
body {
  background: #f1f1f1;
}
#root {
  width: 100%;
  height: 100%;
}
/* Our App Container */
.container {
  padding-bottom: 80px;
}
/* The header */
.header {
  font-size: 22px;
  font-weight: bold;
  font-style: italic;
  color: #f1f1f1;
  background: #10547c;
  padding: 40px;
}
/* The note */

.noteContainer{
  position: relative;
  color: #413d3d;
  margin: 20px;
  background: #FCFCFC;
  border: solid 1px #e9e9e9;
  cursor: pointer;
  box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
  -webkit-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
  -moz-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
  transition: transform .2s ease;
}
.noteText{
  margin: 40px;
}
.note {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.textInNote{
  margin:40px;
  
}

.overlay{
  z-index: 9;
  background-color: rgba(0,0,0,0.2);
  padding: 40px;
  position: relative;
  opacity: 0;
}

.middle{
  text-align: center;
  color:black;
}

.noteContainer:hover {
  transform: scale(1.02);

}
.noteContainer:hover .overlay {
  transform: scale(1.02);
  opacity: 1;
}
/* The add note button */
.btn {
  position: fixed;
  z-index: 1;
  right: 40px;
  bottom: 40px;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  text-align: center;
  line-height: 80px;
  font-size: 40px;
  font-weight: bold;
  color: #f1f1f1;
  background: #10547c;
  cursor: pointer;
  box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
  -webkit-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
  -moz-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
  transition: transform .06s ease;
}
.btn:hover {
  transform: scale(1.2);
}
/* Textinput */
.textInput {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  padding: 0 20px;
  border: none;
  outline: none;
  background: #10547c;
  font-size: 16px;
  color: #f1f1f1;
}